import React from 'react';
import kutubxona from '../Images/kitob.png';
import {Link} from 'react-router-dom' 

const Nav = () => {
    return (
        <div className="Nav">
                    <div class="header">
                    <img src={kutubxona} alt="" className="img"/>
                    </div>
                    <div id="navbar">
                    <Link  className="active" to='/'>Bosh Sahifa</Link>
                    <Link to='/authors' >Kitoblar</Link>
                    {/* <Link to='/dev'>Dasturchi</Link> */}
                    </div> 
                   
        </div>
    );
};


export default Nav;