import React from 'react';
import Nav from './components/Nav';
import Home from './components/Home';
import Dev from './components/Dev';
import Footer from './components/Footer'
import Authors from './components/Authors';
import Kitoblar from './components/Kitoblar'
import aq from './kitoblar/abdulla-qahhor/Abudullaqahhor';
import an from "./kitoblar/alisher-navoiy/Alishernavoiy";
import gg from "./kitoblar/gafurgulom/Gafurgulom";
import oy from "./kitoblar/odil-yoqubov/Odilyoqibov";
import oh from "./kitoblar/otkir-hoshimov/Otkirhoshimov";
import sa from "./kitoblar/said-ahmad/Saidahmad";
import z from "./kitoblar/zulfiya/Zulfiya";
import {BrowserRouter, Route ,Switch} from 'react-router-dom'

const App = () => {
  return ( 
  <BrowserRouter>
    <div className="app">
      <Nav />
        {/* <Switch> */}
          <Route exact path="/" component={Home} />
          <Route path="/kitoblar" component={Kitoblar} />
          <Route path="/dev" component={Dev} />
          <Route path="/authors" component={Authors} />
          {/* authors */}
          <Route path='/abdullaqahhor' component={aq}/>
          <Route path='/alishernavoiy' component={an}/>
          <Route path='/gafurgulom' component={gg}/>
          <Route path='/odilyoqubov' component={oy}/>
          <Route path='/otkirhoshimov' component={oh}/>
          <Route path='/saidahmad' component={sa}/>
          <Route path='/zulfiya' component={z}/>
        {/* </Switch> */}
        <Footer />
    </div>
  </BrowserRouter>
  );
};


export default App;