import React from 'react';
import jpg from "./od.jpg";
import kitob1 from "./Odil Yoqubov Osiy banda.pdf";
import kitob2 from "./Odil Yoqubov. Ko'hna dunyo (roman).pdf";
const Odilyoqibov = () => {
    return (
        <div className='kitob'>
               <div className='flex'>
                    <div class="column">
                        <div class="content">
                            <img src={jpg} alt="Nature" className='jpg' />
                            <h2>Osiy banda</h2>
                            <a className="btn" href={kitob1} download>Yuklab olsih</a>
                        </div>
                    </div>    
                    {/* next */}
                    <div class="column">
                        <div class="content">
                            <img src={jpg} alt="Nature" className='jpg' />
                            <h2>Ko'hna dunyo (roman)</h2>
                            <a className="btn" href={kitob2} download>Yuklab olish</a>
                        </div>
                    </div>
                    </div>
        </div>
    );
};

export default Odilyoqibov;