import React from 'react';
import jpg from "./gg.jpg";
import kitob1 from "./G'afur G'ulom. Shum bola (qissa).pdf";

const Gafurgulom = () => {
    return (
        <div className='kitob'>
               <div className='flex'>
                    <div class="column">
                        <div class="content">
                            <img src={jpg} alt="Nature" className='jpg' />
                            <h2>Shum Bola</h2>
                            <a className="btn" href={kitob1} download>Yuklab olsih</a>
                        </div>
                    </div>    
                    </div>
        </div>
    );
};

export default Gafurgulom;