import React from 'react';
import jpg from "./sa.jpg";
import kitob1 from "./Said Ahmad. Tanlangan asarlar. 2-jild. Hajviyalar, pyesalar.pdf";
const Saidahmad = () => {
    return (
        <div className='kitob'>
               <div className='flex'>
                    <div class="column">
                        <div class="content">
                            <img src={jpg} alt="Nature" className='jpg' />
                            <h2>Tanlangan asarlar. 2-jild. Hajviyalar, pyesalar</h2>
                            <a className="btn" href={kitob1} download>Yuklab olsih</a>
                        </div>
                    </div>    
                    </div>
        </div>
    );
};

Saidahmad.propTypes = {};

export default Saidahmad;