import React from 'react';
import jpg from "./ot.jpg";
import kitob1 from "./O'TKIR HOSHIMOV. Bahor qaytmaydi.pdf";
import kitob2 from "./O'tkir Hoshimov. Dunyoning ishlari.pdf";
import kitob3 from "./O'TKIR HOSHIMOV. Nur borki, soya bor.pdf";
import kitob4 from "./O'TKIR HOSHIMOV. O'zbeklar.pdf";
import kitob5 from "./O'TKIR HOSHIMOV. Sevgi qissalari.pdf";
import kitob6 from "./O'tkir Hoshimov. Tushda kechgan umrlar.pdf";
const Otkirhoshimov = () => {
    return (
        <div classname='kitob'>
             <div className='flex'>
                    <div class="column">
                        <div class="content">
                            <img src={jpg} alt="Nature" className='jpg' />
                            <h2>Bahor qaytmaydi</h2>
                            <a className="btn" href={kitob1} download>Yuklab olsih</a>
                        </div>
                    </div>    
                    {/* next */}
                    <div class="column">
                        <div class="content">
                            <img src={jpg} alt="Nature" className='jpg' />
                            <h2>Dunyoning ishlari</h2>
                            <a className="btn" href={kitob2} download>Yuklab olish</a>
                        </div>
                    </div>
                    {/* next */}
                    <div class="column">
                        <div class="content">
                            <img src={jpg} alt="Nature" className='jpg' />
                            <h2>Nur borki soya bor</h2>
                            <a className="btn" href={kitob3} download>Yuklab olish</a>
                        </div>
                    </div>
                    {/* next */}
                    <div class="column">
                        <div class="content">
                            <img src={jpg} alt="Nature" className='jpg' />
                            <h2>O'zbeklar</h2>
                            <a className="btn" href={kitob4} download>Yuklab olish</a>
                        </div>
                    </div>
                </div>

                {/* two flex */}
                 
                <div className='flex'>
                    <div class="column">
                        <div class="content">
                            <img src={jpg} alt="Nature" className='jpg' />
                            <h2>Sevgi qissalari</h2>
                            <a className="btn" href={kitob5} download>Yuklab olsih</a>
                        </div>
                    </div>    
                    {/* next */}
                    <div class="column">
                        <div class="content">
                            <img src={jpg} alt="Nature" className='jpg' />
                            <h2>Tushda kechgan umrlar</h2>
                            <a className="btn" href={kitob6} download>Yuklab olish</a>
                        </div>
                    </div>
                </div>
        </div>
    );
};

export default Otkirhoshimov;